import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import {createProject } from '../../store/actions/projectActions'
import ImageUpload, {k} from '../ImageUpload';



class createproject extends Component {
  
  state = {
    title: '',
    content: '',
    image:'',
    Likes: 0,
    AuthorId:this.props.auth.uid,
    AuthorFirstName: this.props.profile.firstName,
    AuthorLastName: this.props.profile.lastName
  }
 
  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value,     
    })
  }

  
  handleSubmit = (k) => {
   if(k)
    this.setState({
      image: k
    })
    
    
  }

  fun=()=>{
    if(this.state.title)
    { 
      console.log(this.state) 
      this.props.createProject(this.state);
      this.props.history.push('/')
    }
    
  }
  
  render() {
    const {auth}= this.props;
    if(!auth.uid)return  <Redirect to='/SignIn'/>

    return (
      <div className="container"><br/><br/>
        <div className="white mar " >
          <h5 className="grey-text text-darken-3">Create New Project</h5>
          <div className="input-field">
            <label htmlFor="title">Title</label>
            <input type="text" id='title' onChange={this.handleChange} />
          </div>
          <div className="input-field">
            <label htmlFor="content">Project Content</label>
            <textarea id="content" class="materialize-textarea" onChange={this.handleChange} ></textarea>
          </div>
          <div className="input-field grey-text">Add Images
          <ImageUpload/>
          </div>
          <div className="input-field">
          
           <button className="btn pink lighten-1 z-depth-0" onClick={()=>this.handleSubmit(k)}>1.Done</button>
           --------âž¤
           <button className="btn pink lighten-1 z-depth-0" onClick={this.fun}>2.Post Project</button>
          </div>
        </div>
      </div>
    )
  }
}


const mapDiaptchToProps=(dispatch)=>{
  return{
    createProject:(project)=>{dispatch(createProject(project))}
  }

}

const mapStateToProps=(state)=>{
  return {
    auth: state.firebase.auth,
    profile: state.firebase.profile
  }
}


export default connect(mapStateToProps, mapDiaptchToProps)(createproject)